import './Modal.scss';

const Modal = ({ active, setActive, name }) => {

    return (

        <div className={active ? "modal active" : "modal"} >
            <div className={active ? "modal__content active" : "modal__content"} onClick={e => e.stopPropagation()} >
                <div className="welcome" >Спасибо {name}! </div>
                <div className="goodbye" >Мы скоро с вами свяжемся</div>
                <button type="button" className="buttonM" onClick={() => setActive(false)} >Понятно</button>
                <div className="closeM" onClick={() => setActive(false)}> <img src="Close.png" alt="close" />  </div>
            </div>

        </div>
    )
}

export default Modal
