import React from 'react';
import styles from './App.module.scss';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import Modal from './Modal/Modal';
import Info from './Modal/Info';
import { useState } from 'react';
import classNames from 'classnames';


function App() {
  const [modalActive, setModalActive] = useState(false)
  const [infoActive, setInfoActive] = useState(false)
  const [name, setName] = useState('')

  const SignupSchema = Yup.object().shape({
    firstName: Yup.string()
      .matches(/^[^a-zA-Z0-9 ]+$/g, 'В имени могут быть только буквы')
      .required('Укажите имя'),

    lastName: Yup.string()
      .matches(/^[^a-zA-Z0-9 ]+$/g, 'В фамилии могут только буквы')
      .required('Укажите фамилию'),

    email: Yup.string().email('Проверьте правильность почты').required('Укажите электронную почту'),

    gender: Yup.string().required("укажите пол"),

    gitHub: Yup.string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        'Проверьте правильность ссылки'),
    toggle: Yup.boolean().oneOf([true]),

    profile: Yup.mixed()
      .test('fileSize', <div>Загружайте файл размером  <div>не более 300 байт</div></div>, value => {
        if (value) {
          const sizeInBytes = 300
          return value.size <= sizeInBytes
        } else return true
      })
  });

  return (
    <div>
      <h1>Анкета соискателя</h1>
      <h3>Личные данныe</h3>

      <Formik
        initialValues={{
          firstName: '',
          lastName: '',
          email: '',
          gender: '',
          gitHub: '',
          toggle: false,
          profile: ''
        }}

        validationSchema={SignupSchema}

        onSubmit={(values, { resetForm }) => {
          console.log(values);
          setModalActive(true)
          setName(values.firstName)
          resetForm()
        }}>

        {({ values, errors, touched, isValid, setFieldValue, resetForm }) => (
          <Form>
            <div className={classNames(styles.divFirstName)}>
              <div className={classNames(styles.label, styles.label__firstName)}><label htmlFor={"firstName"}  >Имя *</label></div>
              {errors.firstName && touched.firstName ? (
                <Field name="firstName" type="text" className={classNames(styles.input, styles.error, styles.input__firstName)} />
              ) : <Field name="firstName" type="text" placeholder="Имя" className={classNames(styles.input, styles.input__firstName)} />}
              {errors.firstName && touched.firstName ? (
                <div className={classNames(styles.error__div, styles.error__div__firstName)}>{errors.firstName}</div>
              ) : null}
            </div>

            <div className={styles.divLastName}>
              <div className={classNames(styles.label, styles.label__lastName)}><label htmlFor={"lastName"}  >Фамилия *</label></div>
              {errors.lastName && touched.lastName ? (
                <Field name="lastName" type="text" className={classNames(styles.input, styles.error, styles.input__lastName)} />
              ) : <Field name="lastName" type="text" placeholder="Фамилия" className={classNames(styles.input, styles.input__lastName)} />}
              {errors.lastName && touched.lastName ? (
                <div className={classNames(styles.error__div, styles.error__div__lastName)}>{errors.lastName}</div>
              ) : null}
            </div>

            <div className={classNames(styles.divEmail)}>
              <div className={classNames(styles.label, styles.label__email)}><label htmlFor={"email"}  >Почта *</label> </div>
              {errors.email && touched.email ? (
                <Field name="email" type="email" className={classNames(styles.input, styles.error, styles.input__email)} />
              ) : <Field name="email" type="email" placeholder="Электронная почта" className={classNames(styles.input, styles.input__email)} />}
              {errors.email && touched.email ? <div className={classNames(styles.error__div, styles.error__div__email)}>{errors.email}</div> : null}
            </div>

            {!values.profile ? (
              <div className={styles.load}>
                <input type="file" id="profile" name="profile" className={styles.hide} onChange={(event) => {
                  console.log(event.target.files[0])
                  setFieldValue("profile", event.target.files[0])
                }} />
                <label className={styles.load__label} htmlFor="profile">Загрузить резюме </label>
              </div>)

              : (<div className={(!errors.profile ? (classNames(styles.success__load)) : (classNames(styles.success__load, styles.error__load)))}  >
                <div className={styles.imgAfter} ><img src="Skrepka.png" /></div>
                {values.profile.name}
                <div className={styles.imgBefore}><img src="Krestik.png" /></div>
                {errors.profile ? (<div className={styles.error__input__profile}>{errors.profile}</div>) : null}
              </div>)
            }

            <span className={classNames(styles.radioGroup)}>Пол *</span> {touched.gender ?
              <span className={styles.error && styles.errors__radio}>{errors.gender}</span> : null}
            <div id="radioGroup" error={errors.radioGroup} touched={touched.radioGroup} className={styles.gender}>
              <div >
                <label>
                  <Field type="radio" name="gender" value="мужской" className={styles.input__gender} />
                  Мужской
                </label>
                <label>
                  <Field type="radio" name="gender" value="женский" className={styles.input__gender, styles.input__gender_female} />
                  Женский
                </label>
              </div>
            </div>

            <div className={styles.git__text} >Github</div>
            <div>
              <div className={styles.git__label}><label htmlFor={"gitHub"} >Вставьте ссылку на Github</label></div>
              {errors.gitHub ? (
                <Field name="gitHub" type="text" className={styles.input__git, styles.error__git} />
              ) : <Field name="gitHub" type="text" placeholder="Вставьте ссылку на Github" className={styles.input__git} />}
              {errors.gitHub ? (
                <div className={styles.error__div__git}>{errors.gitHub}</div>
              ) : null}
            </div>

            <div className={styles.check}>
              <label className={styles.label__check}>
                <Field type="checkbox" name="toggle" className={styles.input__check} />
                * Я согласен с <a onClick={() => setInfoActive(true)} >политикой конфидициальности</a>
              </label>
            </div>

            <button type="submit" className={styles.button} disabled={!isValid} >Отправить</button>
          </Form>
        )}
      </Formik>
      <Modal active={modalActive} setActive={setModalActive} name={name} setName={setName} />
      <Info active={infoActive} setActive={setInfoActive} />
    </div>
  );
}

export default App;
